<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="dialogFormClass = true">添加班级</el-button>
			<!-- <el-button type="primary">批量录入学生（上传EXCEL文件）</el-button> -->
		</div>
		<el-table :data="tableDate" ref="multipleTable" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="班级ID" align="center"></el-table-column>
			<el-table-column prop="grade" label="年级" align="center"></el-table-column>
			<el-table-column prop="title" label="班级" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<!-- <el-button size="mini" type="primary" @click="addStudent(scope.$index, scope.row)">添加学生</el-button> -->
					<el-button size="mini">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>


		<el-dialog title="添加班级" :visible.sync="dialogFormClass">
			<el-form :model="form" label-width="80px">
				<el-form-item label="选择年级">
					<el-select v-model="grade" placeholder="请选择年级">
						<el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="班级名称">
					<el-input v-model="form.title"></el-input>
				</el-form-item>
				<div class="submit">
					<el-form-item>
						<el-button type="primary" @click="newClass">提交</el-button>
					</el-form-item>
				</div>
			</el-form>
		</el-dialog>

		<!-- <el-dialog title="添加学生" :visible.sync="addStudentForm">
			<div class="btn">
				<el-input v-model="searchNumber" placeholder="输入学生学号" class="search" @keyup.enter.native="search"></el-input>
				<el-button type="primary" @click="search">搜索</el-button>
			</div>
			<el-table :data="searchTableDate" ref="multipleTable" class="aip-table" @selection-change="handleSelectionChange">
				<el-table-column label="名称" type="selection" align="center"></el-table-column>
				<el-table-column prop="id" label="学生ID" align="center"></el-table-column>
				<el-table-column prop="number" label="学号" align="center"></el-table-column>
				<el-table-column prop="name" label="姓名" align="center"></el-table-column>
				<el-table-column prop="sex" label="性别" align="center">
					<template slot-scope="scope"><span v-text="scope.row.sex == '1' ? '男' : '女'"></span></template>
				</el-table-column>
				<el-table-column prop="age" label="年龄(岁)" align="center"></el-table-column>
				<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			</el-table>
			<div class="submit">
				<el-button type="primary" @click="subStudent">提交</el-button>
			</div>
		</el-dialog> -->
	</div>
</template>

<script>
	import API from '@/api/modules/student.js'
	
	export default {
		data() {
			return {
				dialogFormClass: false,
				// addStudentForm: false,
				tableDate: [],
				gradeList: [],
				form: {
					grade_id: '',
					title: ''
				},
				grade: '',
				disabledDel: true,
				currentPage: 1,
				totalPage: 0,
				// searchTableDate: [],
				// searchNumber: '',
				
			}
		},
		mounted() {
			this.getGrade();
			this.getClass();
		},
		methods: {
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					self.gradeList = res.data;
				})
			},
			getClass() {
				var self = this;
				API.classes(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newClass() {
				var self = this;
				self.gradeList.forEach(item => {
					if (self.grade === item.id) {
						self.form.grade_id = item.id
					}
					console.log(self.form.grade_id)
				})
				API._class(self.form).then(response => {
					self.dialogFormClass = false;
					self.$message.success('提交成功')
					self.getClass();
					self.grade = ''
					self.form = {}
				})
			},
			// // 搜索添加学生
			// search() {
			// 	var self = this;
			// 	API.search(self.searchNumber).then(res => {
			// 		self.searchTableDate = res.data;
			// 		self.totalPage = 1,
			// 		self.searchNumber = '',
			// 		self.$message.success('搜索成功！');
			// 	})
			// },
			// addStudent(index, row) {
			// 	var self = this;
			// 	self.addStudentForm = true;
			// },
			// // 提交选中的学生
			// subStudent(rows) {
				
			// },
			
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getClass();
			},
		}
	}
</script>

<style lang="scss" scoped>
	.btn{
		margin-bottom: 1vw;
	}
	
	.search {
		width: 300px;
		display: inline-block;
		margin: 0 10px;
	}

	.el-table {
		border: 1px solid #dfe6ec;
	}

	.block {
		text-align: center;
		margin-top: 1vw;
	}

	.submit {
		margin-top: 1vw;
		text-align: right;
	}
</style>
